@import '../App.css';

.footer{
    position: fixed;
    bottom: 0;
    left: 0;
    height: 3.4rem;
    width: 100vw;
    background-color: #FF8383;
    display: flex;
}
.footer div{
    flex: 1;
    height: 1;
    background-color: #FF8383;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    font-weight: 400;
}
.footer div:active{
    background-color: #ff6666;
}
.footer div:nth-child(1){
    border-right: 1px solid rgb(139, 0, 0);
}