@import '../App.css';

.maincontent{
    width: 100vw;
    flex: 1 ;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    color: var(--font-color);
}
.holdTasks{
    max-height: 80vh;
    min-height: 80vh;
    height: 100%;
    overflow-y: scroll;
    flex: 0.5;
}
.borderBetween{
    height: 80vh;
    min-width: 2px;
    background-color: var(--font-color);
}

.tasks, .completed{
    display: flex;
    flex-direction: column;
    align-items: center;
}