.taskItem{
    background-color: var(--task-col);
    min-height: 3rem;
    max-height: fit-content;
    min-width: 70%;
    max-width: 70%;
    margin-bottom: 1.5rem;
    border: 2px solid #9E9E9E;
    user-select: none;
    display: flex;
}

.statusIcon{
    min-height: 100%;
    width: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.7rem;
    margin: 0;
    color: #9E9E9E;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.statusIcon:active{
    background-color: rgb(131, 131, 131);
}
.contentHolder{
    min-height: 100%;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.contentHolder:active{
    background-color: rgb(131, 131, 131);
}
p{
    margin: 0;
}
.titleName{
    font-size: 1.1rem;
    font-weight: bold;
    margin-top: 0.5rem;
    margin-bottom: 0.5rem;
    color: var(--font-color);
}

.desccov{
    font-size: 0.9rem;
    margin-top: 0;
    margin-bottom: 0.5rem;
    color: var(--description);
    white-space: pre-wrap;
    white-space: -moz-pre-wrap;
    white-space: -pre-wrap;
    white-space: -o-pre-wrap;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

@media screen and (max-width:1109px){
    .taskItem{
        min-width: 90%;
        max-width: 90%;
        max-height: fit-content;
    }
    .taskItem:last-child{
        margin-bottom: 6rem;
    }
    .statusIcon{
        min-height: 3rem;
        max-height: 100%;
    }
}