@import '../App.css';

.navigator{
    position: sticky;
    top: 0;
    left: 0;
    width: 100vw;
    min-height: 3.8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: var(--font-color);
}

.logo{
    user-select: none;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    font-size: 1.5rem;
    font-weight: bold;
    margin-left: 2rem;
}

.menus{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}
.menus div{
    height: 2.4rem;
    width: 7rem;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 2rem;
    user-select: none;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    border: 2px solid var(--font-color);
    border-radius: 5px;
    font-weight: bold;
}
.menus div:active{
    transform: scale(0.96);
}

@media screen and (max-width:1109px){
    .navigator{
        min-height: 3.8rem;
        z-index: 95;
    }
    .logo{
        margin-left: 1.5rem;
    }
    .menuContainer{
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        height: 100vh;
        background-color: rgba(0, 0, 0, 0.534);
        z-index: 800;
        display: none;
        
    }
    .menuContainer.active{
        display: flex;
    }
    .menuItems{
        position: fixed;
        display: flex;
        flex-direction: column;
        align-items: center;
        background-color: var(--task-col);
        top:4rem;
        left:100vw;
        width: 10rem;
        border-bottom-left-radius: 10px;
        border-top-left-radius: 10px;
        overflow: hidden;
        transition: 0.2s all ease;
        visibility: hidden;
        border: 2px solid var(--font-color);
        border-right: none;
    }
    .menuItems.active{
        visibility: visible;
        transform: translateX(-100%);
        transition: 0.2s ease;
        
    }
    .menuItems ul{
        list-style: none;
        padding-left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .menuItems li{
        min-height: 3rem;
        width: 100%;
        flex: 1;
        background-color: inherit;
        color: var(--font-col);
        border: none;
        border-radius: 5px;
        outline: none;
        font-size: 1.25rem;
        font-weight: bold;
        user-select: none;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        
    }
    .menuItems li:active{
        background-color: grey;
    }
    .menuItems li span{
        margin-left: 2.5rem;
    }

    .bar{
        display: flex;
    }
    .menubtn-bar{
        display: flex;
        justify-content: center;
        align-items: center;
        outline: none;
        border: 0px solid black;
        border-radius: 50%;
        color: inherit;
        background-color: inherit;
        height: 2.5rem;
        width: 2.5rem;
        font-size: 1.2rem;
        font-weight: bold;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
        margin-right: 0.4rem;
        transform: rotate(0deg);
        transition: all 0.3s ease;
    }
    .menubtn-bar.active{
        transform: rotate(90deg);
        transition: all 0.3s ease;
        
    }
}