@import '../App.css';

.holder{
    height: 100vh;
    overflow-y: scroll;
}
.tasks2{
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.holder2{
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    background-color: var(--body-background);
    z-index: 99;
    transform: translateX(100%);
    transition: all 0.3s ease;
}
.holder2.active{
    transform: translateX(0);
}
.logo1{
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3.8rem;
    font-size: 1.4rem;
    color: var(--font-color);
}