@import '../App.css';

.popUpTask, .popUpTask2{
    position: fixed;
    top: 100%;
    left: 50%;
    transform: translate(-50%, 0%);
    height: 70vh;
    width: 50vw;
    background-color: var(--popup-back);
    border-radius: 1rem;
    color: var(--font-color);
    z-index: 101;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease;
}
.popUpTask.active, .popUpTask2.active{
    transform: translate(-50%, -120%);
    transition: all 0.3s ease;
}

.undoTask{
    position: fixed;
    top: 100%;
    left: 50%;
    transform: translate(-50%, 0%);
    height: 20vh;
    width: 20vw;
    background-color: var(--popup-back);
    border-radius: 1rem;
    color: var(--font-color);
    z-index: 101;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease;
}
.undoTask.active{
  top: 50%;
  transform: translate(-50%, -50%);
  transition: all 0.3s ease;
}
.undoTask p{
  margin-bottom: 1.5rem;
}
#TaskTitle, #TaskTitle2{
    width: 80%;
    height: 2rem;
    font-size: 1rem;
    margin-bottom: 2rem;
    border: none;
    border-radius: 5px;
    color: inherit;
    outline: none;
    background-color: var(--popup-input);
}

.opacitorPopup, .opacitorPopup2, .opacitorPopup3{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.527);
    z-index: 100;
    display: none;
}
.opacitorPopup.active, .opacitorPopup2.active, .opacitorPopup3.active{
    display: block;
}
#details, #details2{
    width: 80%;
    height: 15rem;
    font-size: 1rem;
    border: none;
    outline: none;
    color: inherit;
    border-radius: 5px;
    background-color: var(--popup-input);
    margin-bottom: 2rem;
}
.AddBtn{
    width: 5rem;
    height: 2.5rem;
    background-color: var(--font-color);
    color: var(--body-background);
    border: none;
    font-size: 1.2rem;
    cursor: pointer;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
.AddBtn:active{
    transform: scale(0.97);
}


/* downloaded from web*/

.wrapper{
    display: inline-flex;
    height: 2.3rem;
    width: 80%;
    align-items: center;
    justify-content: space-evenly;
    border-radius: 5px;
    margin-bottom: 2rem;
  }
  .wrapper .option{
    background: #fff;
    height: 100%;
    width: 8rem;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    user-select: none;
  }
  
  input[type="radio"]{
    display: none;
  }
  #option-1:checked:checked ~ .option-1,
  #option-2:checked:checked ~ .option-2{
    border-color: #0069d9;
    background: #0069d9;
  }
  #option-1:checked:checked ~ .option-1 .dot,
  #option-2:checked:checked ~ .option-2 .dot{
    background: #fff;
  }
  #option-1:checked:checked ~ .option-1 .dot::before,
  #option-2:checked:checked ~ .option-2 .dot::before{
    opacity: 1;
    transform: scale(1);
  }
  .wrapper .option span{
    font-size: 20px;
    color: #808080;
  }
  #option-1:checked:checked ~ .option-1 span,
  #option-2:checked:checked ~ .option-2 span{
    color: #fff;
  }

  @media screen and (max-width:1109px)  {
    .popUpTask, .popUpTask2{
        height: 22rem;
        width: 95%;
        transform: translate(-50%, 0%);
    }
    .popUpTask.active, .popUpTask2.active{
      transform: translate(-50%, -103%);
    }
    .undoTask{
      width: 70%;
    }
    #TaskTitle, #TaskTitle2{
        width: 90%;
        margin-bottom: 1rem;
    }
    #details, #details2{
        width: 90%;
        height: 10rem;
        margin-bottom: 1rem;
    }
    .wrapper{
        width: 90%;
        justify-content: space-between;
        margin-bottom: 1rem;
    }
  }