:root{
  --body-background:#11191b;
  --font-color: #E5E5E5;
  --description: #A3A3A3;
  
  --popup-back: #1d2b2e;
  --popup-input: #273c41;

  --task-col: #3D3D3D;
}
[data-theme="light"]{
  --body-background:#ffffff;
  --font-color: #000000;
  --description: #707070;

  --popup-back: #88a2a7;
  --popup-input: #afced6;

  --task-col: #cde1e6;
} 
.App {
  max-width: 100vw;
  max-height: 100vh;
  min-height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;
  background-color: var(--body-background);
  display: flex;
  flex-direction: column;
}

@media screen and (min-width:1109px)  {
  /* width */
  ::-webkit-scrollbar {
    width: 7px;
  }

  /* Track */
  ::-webkit-scrollbar-track {
    background-color: transparent;
    transition: 1s ease;
  }
  ::-webkit-scrollbar-track:hover{
    background: var(--item-back); 
    border-radius: 7px;
    transition: 1s ease;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: var(--font-col); 
    border-radius: 7px;
  }

  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: var(--item-border); 
  }
}