@import '../App.css';

.menusSet{
    height: 100vh;
    overflow-y: scroll;
}
.indSet{
    min-height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: var(--font-color);
}
.indSet div{
    width: 100%;
    height: 4rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 1.2rem;
}
.indSet div:active{
    background-color: grey;
}
.indSet div span{
    margin-left: 1rem;
}
.indSet div i{
    margin-right: 1rem;
}

.settings{
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left: 0;
    background-color: var(--body-background);
    z-index: 99;
    transform: translateX(100%);
    transition: all 0.3s ease;
}
.settings.active{
    transform: translateX(0);
}

.backbtn{
    height: 2rem;
    width: 5rem;
    position: fixed;
    top: 0.9rem;
    right: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--font-color);
    border: 2px solid var(--font-color);
    border-radius: 7px;
    font-size: 1.1rem;
    cursor: pointer;
    user-select: none;
}
.backbtn:active{
    transform: scale(0.95);
}

@media screen and (max-width:1109px){
    .backbtn{
        display: none;
    }
}